<template>
	<div>
		<vue-element-loading 
			:active="isLoading"
			:background-color="'rgba(255, 255, 255, .2)'"
			:color="'blue'"
		/>
		<vue-good-table
			ref="table"
			max-height="100vh"
			class="table"
			:columns="collumns"
			:rows="rows"
			:search-options="{
				enabled: true,
				placeholder: '  '
			}"
		>	
			<div slot="table-actions">
				<BaseButton
					id="table_export_excel"
					class="button"
					:color="'gray'"
					@click="exportData"
				>
					<i class="fa-solid fa-file-excel mr-2" />
					<span class="button__title">  EXCEL</span>
				</BaseButton>
				<BaseButton
					id="table_datepicker"
					class="button"
					:color="'gray'"
					@click="openDatePicker"
				>
					<i class="fa-solid fa-calendar-days mr-2" />
					<span class="button__title">  </span>
				</BaseButton>
			</div>
		</vue-good-table>
		<BaseModal 
			:width="'290px'"
			:open="isDatePickerOpen"
			:title="' '"
			@onModalClose="closeDatePicker"
		>
			<template slot="content">
				<v-date-picker
					v-model="dates"
					class="date_picker"
					range
				/>
				<BaseButton
					id="date_picker__btn-filter"
					class="button"
					:color="'gray'"
					@click="getFiltredData"
				>
					<span class="button__title"></span>
				</BaseButton>
			</template>
		</BaseModal>
	</div>
</template>

<script>
import shepherdMixin from '@/mixins/shepherdMixin';
import { HTTP } from '@/helpers/httpCommon';
import { mapGetters } from 'vuex';
import { collumns } from './ReportListCollums.js';
import 'vue-good-table/dist/vue-good-table.css';
import { VueGoodTable } from 'vue-good-table';
import BaseModal from '@/components/BaseModal.vue';
import BaseButton from '@/components/BaseButton.vue';
import exportFromJSON from 'export-from-json';
import dayjs from 'dayjs';
export default {
	components: {
		'vue-good-table': VueGoodTable,
		BaseModal,
		BaseButton,
	},
	mixins: [shepherdMixin],
	data() {
		return {
			isLoading: false,
			isDatePickerOpen: false,
			dates: [],
			rows: [],
		};
	},
	computed: {
		...mapGetters(['getMyRole']),
		userRole() {
			return this.getMyRole;
		},
		collumns() {
			return collumns[this.userRole];
		}
	},
	created() {
		this.getData();
	},
	mounted() {
		if(!localStorage.getItem('CompleteReportTour')) {
			this.createTour();
			this.addReportTour();
			this.tour.start();
		}
	},
	methods: {
		getData(id = this.$route.params.userId) {
			this.isLoading = true;

			const body =  {
				time_filter:{
					time_start: this.dates[0] ? dayjs(this.dates[0]).toISOString() : null,
					time_end: this.dates[1] ? dayjs(this.dates[1]).toISOString() : null
				}
			};

			if(id) body.username = id;

			HTTP.post('get/messages', body)
				.then((response) => {

					if(response.data.Data) {
						response.data.Data.forEach(item => {
							item.timestamp = dayjs(item.timestamp).format('YYYY-MM-DD HH:mm'); 
						});
					}

					this.rows = response.data.Data || [];
				})
				.catch(error => {
					this.$toast.error(error.message, {
						timeout: 2000
					});
				})
				.finally(() => {
					this.isLoading = false;
				});
		},
		closeDatePicker() {
			this.dates = [];
			this.isDatePickerOpen = false;
		},
		openDatePicker() {
			this.isDatePickerOpen = true;
		},
		getFiltredData() {
			this.isDatePickerOpen = false;
			this.getData();
		},
		exportData() {
			const mergedArray = this.$refs.table.processedRows[0].children.map((item) => {
				const newItem = {};
				this.collumns.forEach((field) => {
					const { field: oldKey, label } = field;
					newItem[label] = item[oldKey];
				});
				return newItem;
			});
			const fileName = `${dayjs().unix()}_report_${dayjs().format('DD.MM.YYYY')}`;
			const exportType =  exportFromJSON.types.xls;
			exportFromJSON({ data: mergedArray, fileName, exportType});

		},
	},
};
</script>

<style lang="scss" scoped>
	.list-wrapper {
		padding: 20px;
		height: 100%;
	}
	.table::v-deep {
		.vgt-global-search {
			display: flex;
			flex-direction: column;
			border-radius: 8px;
		}
		.vgt-responsive {
			min-height: 100vh;
		}
	}
	.list_item {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-right: 10px;
		height: 70px;
		.block_info {
			&__picture_frame {
				display: flex;
				justify-content: center;
				align-items: center;
				
				width: 40px;
				height: 40px;

				border-radius: 50%;

				background-color: gray;
				color: white;
			}
			&__name {
				margin-left: 15px;
				align-self: center;
				cursor: pointer;
			}
		}
		&__block_info {
			display: flex;
		}
	}
	.button {
		margin: 5px;
		&__title {
			font-weight: 700;
		}
	}
	.date_picker {
		border-radius: 0;
	}
</style>